___CSS_LOADER_EXPORT___.push([module.id, "form #search{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-clip:padding-box;background-color:#fff;border:1px solid #ced4da;border-radius:.25rem;color:#212529;font-size:1rem;font-weight:400;outline:1px auto #ced4da;padding:5px;transition:all .15s ease-in-out;width:30%}form #search:focus-visible{outline:1px auto #b6b6b6}form button{background-color:#0d6efd;border:1px solid transparent;border-radius:5px;color:#fff;font-weight:600;padding:4px 20px}", "",{"version":3,"sources":["webpack://./resources/js/components/users/index.vue"],"names":[],"mappings":"AAkGA,aASA,uBAAA,CACA,oBAAA,CACA,eAAA,CAJA,2BAAA,CADA,qBAAA,CAEA,wBAAA,CAIA,oBAAA,CAPA,aAAA,CAFA,cAAA,CACA,eAAA,CAUA,wBAAA,CAZA,WAAA,CAWA,+BAAA,CAZA,SAeA,CACA,2BACA,wBACA,CACA,YAEA,wBAAA,CACA,4BAAA,CAEA,iBAAA,CADA,UAAA,CAEA,eAAA,CALA,gBAMA","sourcesContent":["<template>\r\n    <div>\r\n        <div class=\"card mb-4\">\r\n            <div class=\"card-header \">\r\n                <span class=\"d-block mb-4 mt-2 fw-bold\">\r\n                    <i class=\"fas fa-table me-1\"></i>\r\n                    Users Table\r\n                </span>\r\n                <form class=\"d-none d-md-inline-block w-100 py-2 align-center\" action=\"#\">\r\n                    <input type=\"text\" id=\"search\" v-model=\"search\" placeholder=\"Search Here By User Name Or Email\">\r\n                    <!-- create user link -->\r\n                    <button class=\"btn-success float-end  d-inline-block \">\r\n                        <router-link :to=\"{ name: 'UserCreate' }\"  class=\"text-decoration-none text-light\">Add User</router-link>\r\n                    </button>\r\n                </form>\r\n            </div>\r\n            <div :class=\"[users.length > 0 ? 'card-body overflow-auto': 'd-none' ]\">\r\n                <table class=\"table table-bordered border-primary\">\r\n                    <thead>\r\n                        <tr>\r\n                            <th>#</th>\r\n                            <th>Name</th>\r\n                            <th>Phone</th>\r\n                            <th>Role</th>\r\n                            <th>Email</th>\r\n                            <th>Action</th>\r\n                        </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                        <tr v-for=\"(user, index )  in filterdusers\" :key=\"user.id\">\r\n                            <td>{{index + 1}}</td>\r\n                            <td>{{user.name}}</td>\r\n                            <td>{{user.phone}}</td>\r\n                            <td>{{user.role}}</td>\r\n                            <td>{{user.email}}</td>\r\n                            <td>\r\n                                <router-link :to=\"{ name: 'UserEdit' , params: { id: user.id }}\"  class=\"btn btn-primary text-decoration-none text-light\">Edit</router-link>\r\n                                <a :class=\"(current.id === user.id)? 'd-none' :''\" @click=\"del(user.id)\" class=\"btn btn-danger text-decoration-none text-light\">Delete</a>\r\n                            </td>\r\n                        </tr>\r\n                    </tbody>\r\n                </table>\r\n            </div>\r\n            <div :class=\"[users.length === 0 ? 'card-body text-center': 'd-none' ]\">{{Loding}}</div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    data(){\r\n        return{\r\n            users:[],\r\n            filterdusers:[],\r\n            search:'',\r\n            current:'',\r\n            Loding:\"Loading\"\r\n        }\r\n    },\r\n    mounted() {\r\n        this.getUsers()\r\n        this.currentuser()\r\n    },\r\n    methods:{\r\n        getUsers(){\r\n// axios.post(\"https://reqres.in/api/articles\", article).then(response => (this.info = response)).catch(error => console.log(error))\r\n            axios.get('http://localhost/NewPh/public/users')\r\n                .then(response => {this.users = response.data;this.filterdusers = response.data;this.Loding=\"There Is No Users\"})\r\n                .catch(error => console.log(error))\r\n                this.search=\"\"\r\n        },\r\n        del(id){\r\n            axios.delete(`http://localhost/NewPh/public/users/${id}`)\r\n                .then(()=>{\r\n                    this.users = this.users.filter((user)=>{return user.id!= id ?  user : \"\"})\r\n                    this.filterdusers = this.users\r\n                    })\r\n        },\r\n        currentuser(){\r\n                axios.get('http://localhost/NewPh/public/current')\r\n                .then(response=>{this.current=response.data})\r\n                .catch(erroe=>console.log('logged out'));\r\n            }\r\n    },\r\n    watch: {\r\n        search() {\r\n            if (this.search.length>0) {\r\n                this.filterdusers = this.users.filter((user)=>{return user.name.toUpperCase().match(this.search.toUpperCase()) || user.email.toUpperCase().match(this.search.toUpperCase()) ?  user : \"\"})\r\n            }\r\n            else{\r\n                this.filterdusers = this.users\r\n            }\r\n        }\r\n    },\r\n}\r\n</script>\r\n\r\n<style>\r\nform #search{\r\n    width: 30%;\r\n    padding: 5px ;\r\n    font-size: 1rem;\r\n    font-weight: 400;\r\n    color: #212529;\r\n    background-color: #fff;\r\n    background-clip: padding-box;\r\n    border: 1px solid #ced4da;\r\n    -webkit-appearance: none;\r\n    -moz-appearance: none;\r\n    appearance: none;\r\n    border-radius: 0.25rem;\r\n    transition: all 0.15s ease-in-out;\r\n    outline: #ced4da auto 1px;\r\n    \r\n}\r\nform #search:focus-visible{\r\n    outline: rgb(182, 182, 182) auto 1px;\r\n}\r\nform button{\r\n    padding: 4px 20px;\r\n    background-color: #0d6efd;\r\n    border: 1px solid transparent;\r\n    color: #FFF;\r\n    border-radius: 5px;  \r\n    font-weight: 600;\r\n}\r\n</style>"],"sourceRoot":""}]);
